@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap&subset=cyrillic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.app-wrapper {
  display: grid;
  grid-template-areas: "header header" "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  max-width: 1200px;
  margin: 0 auto;
}

.content {
  grid-area: content;
  background-color: aqua;
}

/*# sourceMappingURL=main.css.map */

.header {
  grid-area: header;
  background-color: chartreuse;
  text-align: center;
}
.header .header__logo {
  max-height: 101%;
}

/*# sourceMappingURL=header.css.map */

.nav {
  grid-area: nav;
  background-color: coral;
}
.nav .nav__item {
  margin: 10px;
}
.nav .nav__item:last-child {
  margin-top: 25px;
}
.nav .nav__link {
  color: #000;
  font-weight: 600;
}
.nav .nav__link.active {
  color: #fff;
  cursor: default;
  pointer-events: none;
}

/*# sourceMappingURL=nav.css.map */

.post {
  margin-top: 10px;
}
.post .post__body {
  display: flex;
  align-items: center;
}
.post .post__img {
  max-width: 50px;
  border-radius: 50%;
  margin-right: 10px;
}
.post .like__count {
  font-weight: 600;
}

/*# sourceMappingURL=post.css.map */

.my-posts {
  padding: 5px;
}
.my-posts .new__add {
  display: block;
}

/*# sourceMappingURL=my_posts.css.map */

.profile__info {
  padding: 5px;
  display: flex;
}
.profile__info .info__avatar {
  max-width: 150px;
  margin-right: 15px;
}
.profile__info .avatar__img {
  width: 100%;
}
.profile__info .common__item {
  margin-top: 10px;
}

/*# sourceMappingURL=profile-info.css.map */

.profile__header {
  width: 100%;
}
.profile__header .header__img {
  width: 100%;
}

/*# sourceMappingURL=profile.css.map */

.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}
.dialogs .dialogs__names {
  padding: 10px;
}
.dialogs .names__item {
  padding: 5px;
  cursor: pointer;
}
.dialogs .names__item .active {
  background: orange;
  cursor: default;
}
.dialogs .dialogs__messages {
  padding: 10px;
}
.dialogs .messages__item {
  padding: 5px;
}

/*# sourceMappingURL=dialogs.css.map */



